<template>
  <div class="b1container">
    <div class="empty">
        <p class="title" v-text="'处置率'"></p>
        <p class="rate" v-text="rhandle + '%'"></p>
    </div>
  </div>
</template>

<script>
export default {
  name: "Box1",
  props: {
    total: {
      default: 1,
      type: Number,
    },
    handle: {
      default: 0,
      type: Number,
    },
  },
  computed: {
    rhandle() {
      return Math.round((this.handle / this.total) * 100);
    },
  },
};
</script>

