<template>
  <div class="pcontainer">
    <div class="ppp">
      <div class="title">
        <span v-text="'逾期'"></span>
      </div>
      <el-progress
        :text-inside="true"
        :stroke-width="pheigth"
        :percentage="ro"
        :color="'#fabf3d'"
      ></el-progress>
    </div>

    <div class="ppp">
      <div class="title">
        <span v-text="'将到期'"></span>
      </div>
      <el-progress
        :text-inside="true"
        :stroke-width="pheigth"
        :percentage="re"
        :color="'#2c7ad8'"
      ></el-progress>
    </div>

    <div class="ppp">
      <div class="title">
        <span v-text="'及时'"></span>
      </div>
      <el-progress
        :text-inside="true"
        :stroke-width="pheigth"
        :percentage="rt"
        :color="'#09c8ff'"
      ></el-progress>
    </div>
  </div>
</template>

<script>
export default {
  name: "Progress",
  data() {
    return {
      pheigth: 18,
      ro: 0,
      re: 0,
      rt: 0,
      // ro: 50,
      // re: 30,
      // rt: 20,
      startTimer: ["", "", ""],
    };
  },
  props: {
    total: {
      default: 1,
      type: Number,
    },
    overdue: {
      default: 0,
      type: Number,
    },
    expir: {
      default: 0,
      type: Number,
    },
    timely: {
      default: 0,
      type: Number,
    },
  },
  computed: {
    roverdue() {
      return parseInt(Math.round((this.overdue / this.total) * 100));
    },
    rexpir() {
      return parseInt(Math.round((this.expir / this.total) * 100));
    },
    rtimely() {
      return parseInt(Math.round((this.timely / this.total) * 100));
    },
  },
  methods: {
    startp() {
      this.startTimer[0] = setInterval(() => {
        this.ro++;
        if (this.ro >= this.roverdue) {
          clearInterval(this.startTimer[0]);
        }
      }, 20);
      this.startTimer[1] = setInterval(() => {
        this.re++;
        if (this.re >= this.rexpir) {
          clearInterval(this.startTimer[1]);
        }
      }, 40);
      this.startTimer[2] = setInterval(() => {
        this.rt++;
        if (this.rt >= this.rtimely) {
          clearInterval(this.startTimer[2]);
        }
      }, 80);
    },
  },
  watch: {
    roverdue() {
      if (this.ro > this.roverdue) {
        this.startTimer[0] = setInterval(() => {
          this.ro--;
          if (this.ro <= this.roverdue) {
            clearInterval(this.startTimer[0]);
          }
        }, 20);
      }
      if (this.ro < this.roverdue) {
        this.startTimer[0] = setInterval(() => {
          this.ro++;
          if (this.ro >= this.roverdue) {
            clearInterval(this.startTimer[0]);
          }
        }, 20);
      }
    },
    rexpir() {
      if (this.re > this.rexpir) {
        this.startTimer[1] = setInterval(() => {
          this.re--;
          if (this.re <= this.rexpir) {
            clearInterval(this.startTimer[1]);
          }
        }, 20);
      }
      if (this.re < this.rexpir) {
        this.startTimer[1] = setInterval(() => {
          this.re++;
          if (this.re >= this.rexpir) {
            clearInterval(this.startTimer[1]);
          }
        }, 20);
      }
    },
    rtimely() {
      if (this.rt > this.rtimely) {
        this.startTimer[2] = setInterval(() => {
          this.rt--;
          if (this.rt <= this.rtimely) {
            clearInterval(this.startTimer[2]);
          }
        }, 20);
      }
      if (this.rt < this.rtimely) {
        this.startTimer[2] = setInterval(() => {
          this.rt++;
          if (this.rt >= this.rtimely) {
            clearInterval(this.startTimer[2]);
          }
        }, 20);
      }
    },
  },
  created() {
    this.startp();
    // setTimeout(() => {
    //   this.ro -= 20
    // },10000)
  },
};
</script>

<style lang="less" scoped>
* {
  margin: 0;
  padding: 0;
}
.ppp{
  margin-bottom: 6%;
}
.pcontainer {
  box-sizing: border-box;
  width: 100%;
}
.title {
  font-size: 16px;
  // margin: 0 0 1% 10px;
}
</style>

<style scoped>
.el-progress /deep/ .el-progress-bar__inner {
  height: 24px !important;
  border-radius: inherit;
}
.el-progress /deep/ .el-progress-bar__outer {
  height: 24px !important;
  border-radius: inherit;
}
/* .el-progress /deep/ .el-progress-bar{
  height: 20;
} */
</style>
